import { useState } from "react";

import { Button } from "./Button";

const initialState = [{ id: 0 }];

export const ButtonMadness = () => {
  const [idList, setIdList] = useState(initialState);
  const handleUpdateList = () => {
    setIdList((current) => {
      const id = { id: current.length };
      const x = [...current, id];
      return x;
    });
  };

  return (
    <div>
      {idList.map((item) => {
        const { id } = item;
        return <Button key={id} handleUpdateIdList={handleUpdateList} />;
      })}
    </div>
  );
};
